body {
    margin: 0;
    font-family: 'Lato', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
  
h1,h2,h3 {
    font-family: 'Ubuntu', sans-serif;
}

.container {
    padding: 20px 5vw;
    max-width: 1280px;
    margin: auto;
}

.full-width .container {
    padding: 0;
}

.clickable {
    cursor: pointer;
    transition: 0.4s;
}
.clickable:hover {
    transform: scale(1.04);
}

input[type=text], input[type=button] {
    -webkit-appearance: none;
    -webkit-border-radius: 0;
}

ul {
    padding: 0;
}

img {
    width: 100%;
}

.blue {
    color: #005293;
}

.bkg-img {
    background-size: cover;
    background-position: center center;
}

.titlesChico {
    font-family: 'Ubuntu', sans-serif;
}

.titles {
    font-family: 'Ubuntu', sans-serif;
    font-size: 2rem;
}

.white {
    color: white;
}
.center-text {
    text-align: center;
}

.outline.black {
    color: black;
    -webkit-text-fill-color: white; /* Will override color (regardless of order) */
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: black;
}
.outline.white {
    color: white;
    -webkit-text-fill-color: transparent; /* Will override color (regardless of order) */
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: rgba(255, 255, 255, 0.42);
}

.flex {
    display: flex;
    flex-wrap: wrap;
}

.flex.column {
    display: flex;
    flex-direction: column;
}
.space-between {
    justify-content: space-between;
}
.jus-algn-center {
    justify-content: center;
    align-items: center;
}
.relative {
    position: relative;
}
.absolute {
    position: absolute;
}
.black-bkg {
    background-color: #292929;
}

.home-header {
    background-color: #E9E9E9;
}
.home-header h2 {
    font-family: 'Ubuntu', sans-serif;
}

.home-hero-info {
    height: 90vh;
    padding: 0 5vw;
    flex-direction: column;
}
.home-hero-info h2:nth-child(1) {
    font-size: 4rem;
    top: -0.8rem;
    margin: 0;
}

.category-btn {
    border: none;
    width: 45%;
    color: #fff;
    padding: 10px 0;
    margin: 5px 0;
    font-size: 1.1rem;
}
.score-section {
    padding-bottom: 70px !important;
}
.score-btn-container {
    flex-direction: column;
}
.score-btn {
    border: none;
    width: 300px;
    color: #fff;
    padding: 10px 0;
    margin: 5px 0;
    font-size: 1.1rem;
}

.home-hero-img {
    width: 90%;
    display: block;
    margin: auto;
    transform: translateY(-100px);
}
.ages-img {
    margin-top: 40px;
}

.mot {
    padding: 20px 5vw;
}

.motivation {
    flex-direction: column;
}

.motivation .flex {
    width: 80%;
    margin: 20px;
}

.motivation .flex img {
    height: 80px;
width: auto;
}

/* BOOK VIEW */

.libro-header {
    background-color: #292929;
    flex-direction: column;
    padding: 90px 5vw 50px 5vw;
    color: #fff;
}
.libro-top-info {
    font-size: 0.75rem;
    justify-content: space-between;
    margin-bottom: 30px;
}
.libro-top-info div {
    width: 30%;
}
.libro-top-info div p {
    width: 100%;
}
.libro-top-info p {
    width: 30%;
}
.libro-title {
    font-size: 2rem;
    margin-top: 0;
}
.review {
    padding: 40px 0;
}
.review h4 {
    font-size: 1.2rem;
    margin: 0;
}
.review p {
    font-size: 0.95rem;
}
.libro-info-container {
    flex-direction: column-reverse;
    padding: 30px 12%;
}
.libro-info {
    flex-direction: column;
}
.tags p {
    color: #707070;
    background-color: #F0F0F0;
    padding: 10px 20px;
    border-radius: 30px;
    margin: 5px 10px 5px 0;
    font-size: 0.95rem;
}
.libro-info > div > h3 {
    color: #005293;
    font-size: 1.4rem;
}
.libro-info > div > ul > li {
    color: #707070;
    margin: 4px 0;
}

.libro-gallery {
    background-color: #F0F0F0;
    /* padding: 180px 10% 90px 10%; */
    padding: 0px 0% 0px 0%;
    margin-top: 40px;
}
.book-gallery-stage {
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
    flex-wrap: wrap;
}
.book-gallery-stage > div {
    width: 100%;
    display: flex;
    /* width: 17%; */
}
.book-gallery-stage > div img {
    width: 22%;
    margin-right: 1%;
    /* width: 100%; */
}
.book-gallery-stage > img {
    width: 100%;
    margin-top: 10px;
    /* width: 78%; */
    display: block;
}
.us-header {
    color: #fff;
    padding: 90px 5vw 30px 5vw
}
.us-header .container {
    padding: 0;
}

.criterio-container {
    padding: 30px 7% 40px 10%;
}
.criterio-titles {
    position: relative;
}
.criterio-titles h3 {
    position: absolute;
    font-size: 6rem;
    top: -2.5rem;
    left: -20px;
    margin: 0;
    font-weight: 800;
}
.criterio-imgs {
    margin-top: 60px;
    position: relative;
    width: 100%;
    max-width: 350px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.criterio-imgs img {
    filter: grayscale();
}
.criterio-imgs button {
    cursor: pointer;
    background-color: #005293;
    color: #fff;
    padding: 10px 20px;
    border-radius: 20px;
    border: none;
    position: absolute;
    transition: 0.4s;
}
.criterio-imgs:hover > button {
    transform: scale(1.2);
    background-color: #ff843d;
}
.hows {
    background-color: #005293;
    color: #fff;
    padding: 30px 0;
}
.how-container {
    justify-content: space-between;
    align-items: center;
    position: relative;
}
.how-container > img {
    width: 18%;
    height: auto;
}
.how-container div {
    width: 75%;
}
.how-container div h5 {
    font-size: 1.2rem;
    margin-bottom: 15px;
}
.how-container div p {
    font-weight: 300;
}
.how-container div.white-line {
    background-color: #fff;
    width: 3px;
    height: 40%;
    position: absolute;
    top: 75%;
    left: 8%;
}
.how-container  div.white-line {
    background-color: #fff;
    width: 3px;
    height: 40%;
    position: absolute;
    top: 75%;
    left: 8%;
}
.hows > .container > .flex > .how-container:last-child .white-line {
    display: none;
}
.us {
    background-color: #3E3E3E;
    color: #fff;
    padding: 60px 5vw;
}
.profile img {
    width: 250px;
}
.us > .container > .flex {
    margin-top: 50px;
}

/* BOOKS VIEW */

.books-header {
    margin-top: 60px;
    background-position: center top;
    padding: 30px 5vw 30px 5vw;
    position: relative;
}
.books-header .container {
    align-content: center;
    justify-content: center;
    display: flex;
}
.header-big-titles {
    font-size: 4rem;
    position: absolute;
    text-align: center;
    margin: 0;
    top: 15px;
}

.filters {
    padding: 0 5vw;
}

.filters-container {
    /* width: 350px; */
    margin: 20px auto;
}
.filters-container .MuiPaper-root {
    box-shadow: none;
}
.css-1elwnq4-MuiPaper-root-MuiAccordion-root::before {
    background-color: transparent !important;
}
.filters .MuiAccordionSummary-content > p {
    font-size: 0.85rem;
    color: #005293;
}
.MuiAccordionSummary-expandIconWrapper .expand-icon {
    color: #005293 !important;
}
.MuiAccordion-region {
    display: flex;
    justify-content: center;
}
.filters-container .MuiAccordionDetails-root .MuiBox-root {
    max-height: 200px;
    overflow: auto;
}
.filters-container .MuiAccordionDetails-root .MuiBox-root.slider {
    display: flex;
    justify-content: space-between;
    overflow: visible;
    text-align: center;
}
.filters-container .MuiAccordionDetails-root .MuiBox-root.slider > div {
    width: 48%;
}

.scoreIcon {
    width: 80px;
}

.all-books.container {
    max-width: 1700px;
    margin: auto;
}

.filter-btn {
    background-color: #005293 !important;
    color: #fff !important;
    margin-top: 15px !important;
    width: 100%;
    transition: 0.4s !important;
}

.filter-btn:hover {
    background-color: #FF5E00 !important;
    transform: scale(1.04);
}

.book-cards-container {
    margin-top: 140px;
    align-items: flex-start;
}
.book-card {
    background-color: #fff;
    max-width: 280px;
    margin: 0px auto 30px auto;
    display: block;
    position: relative;
    height: auto;
}
.viewMore {
    display: none;
}
.book-card-text {
    padding: 20px 30px;
    background-color: #fff;
    height: 210px;
}
.book-card-text h3 {
    margin-top: 0;
    color: #707070;
    font-weight: 400;
    margin-bottom: 8px;
    font-size: 1.1rem;
}
.book-card-text h2 {
    font-weight: 400;
    font-size: 1.1rem;
    margin-top: 0;
}
.book-card-text .card-score {
    position: absolute;
    bottom: 5%;
}
.book-card-text .card-score p {
    vertical-align: middle;
    color: #454545;
    font-size: 0.85rem;
    margin-bottom: 0;
}

/* MODAL BOX */

.modalBox {
    height: 85vh;
    width: 90vw;
    margin: 10vh 5vw 5vh 5vw;
    z-index: 10;
    position: fixed;
    background-color: #292929;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}
.closeBtn {
    position: absolute;
    width: 50px;
    height: 50px;
    right: 15px;
    top: 10px;
    cursor: pointer;
    transition: 0.4s;
}
.closeBtn:hover {
    transform: scale(1.08);
}

.modalBoxImages > img {
    width: 90%;
}
.modalBoxImages .flex {
    text-align: center;
    align-items: center;
    justify-content: center;
}
.modalBoxImages .flex img {
    width: 15%;
    margin: 1%;
}

.pagination {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 20px 0 40px 0;
}

.scoreIconSmall {
    width: 25px;
    height: 25px;
    vertical-align: middle;
    margin-bottom: 10px;
    margin-right: 10px;
}

/* SEARCH BAR */

.searchBar {
    width: 400px !important;
    margin-bottom: 20px;
    background-color: #fff;
    border: none;
    border-radius: 5px;
}

.searchBar fieldset {
    border: none;
}

.searchBar label {
    color: #005293;
}

.searchBar .MuiInputLabel-root[data-shrink='true'] {
    transform: translate(14px, -18px) scale(0.9) !important;
}

.keywords-label {
    text-transform: capitalize;
}

@media screen and (min-width: 800px) {
    .motivation {
        flex-direction: row;
        justify-content: space-between;
        margin: 50px 0;
        align-items: flex-start;
    }
    .motivation .flex {
        width: 30%;
        margin: 0px;
    }
    .libro-header {
        padding: 120px 5vw 50px 5vw;
    }
    .libro-top-info {
        font-size: 0.9rem;
    }
    .libro-top-info p {
        width: 30%;
    }
    .us-header {
        padding: 150px 5vw 90px 5vw;
    }
    .us-subtext {
        padding: 100px 5vw;
    }
    .criterio-container {
        padding: 100px 5vw;
    }
    .criterio-container > .flex {
        display: flex;
        justify-content: space-between;
        align-items: center;
        max-width: 1100px;
        margin: 0 auto;
    }
    .criterio-texts {
        width: 60%;
    }
    .criterio-imgs {
        margin-top: 0;
        width: 25%;
    }
    .hows > .container {
        max-width: 700px;
    }
    .how-container > img {
        width: 70px;
    }
    .how-container  div.white-line {
        background-color: #fff;
        width: 3px;
        height: 30%;
        position: absolute;
        top: 82%;
        left: 32px;
    }
    .us > .container {
        max-width: 800px;
    }
    .us > .container > .flex {
        justify-content: space-between;
    }
    .profile {
        flex-direction: column;
        width: 30%;
    }
    .profile img {
        width: 100%;
    }
    .us-text {
        width: 60%;
    }

    /* BOOKS VIEW */
    .books-header {
        padding: 50px 5vw 50px 5vw;
        margin-top: 90px;
    }
    .header-big-titles {
        top: 30px;
    }
    .book-cards-container {
        display: flex;
        flex-wrap: wrap;
    }

    .scoreIconSmall {
        width: 25px;
        height: 25px;
        vertical-align: middle;
        margin-bottom: 0px;
        margin-right: 10px;
    }
}

@media screen and (min-width: 1200px) {
    .titles {
        font-size: 2.7rem;
        margin-bottom: 0;
    }
    .home-header {
        padding-bottom: 10vh;
    }
    .home-header .container {
        margin: 0;
        max-width: 100%;
        display: flex;
        position: relative;
    }
    .home-hero-info {
        width: 70%;
        padding-right: 20%;
    }
    .home-hero-info .titles {
        font-size: 2.8rem;
    }
    .home-hero-img {
        position: absolute;
        right: 0;
        top: 40vh;
        display: inline-block;
        width: 40%;
        margin: auto;
        transform: translateY(-100px);
    }
    .mot > .flex > img.ages-img {
        position: relative;
        margin-top: 40px;
        transform: translateY(0px);
    }
    .home-hero-info h2:nth-child(1) {
        position: absolute;
        left: -4vw;
        font-size: 4rem;
        top: 0;
    }
    .category-btn {
        cursor: pointer;
        width: 24%;
        transition: 0.4s;
    }
    .category-btn:hover {
        cursor: pointer;
        transform: scale(1.04);
    }
    .score-btn {
        cursor: pointer;
        width: 320px;
        transition: 0.4s;
    }
    .score-btn:hover {
        cursor: pointer;
        transform: scale(1.04);
    }
    .left-padding {
        padding: 20px 0 0 5vw;
        margin: 0;
    }
    .mot {
        padding-right: 0;
        margin-bottom: 120px;
    }
    .mot > .flex {
        justify-content: space-between;
    }
    .motivation-texts {
        width: 45%;
    }
    .mot > .flex > img {
        width: 50%;
        transform: translateY(50px);
    }
    .libro {
        flex-direction: row-reverse;
    }
    .libro-header {
        width: 55%;
        padding: 170px 7% 0 7%;
    }
    .libro-top-info {
        /* line-height: 0; */
    }
    .libro-title {
        margin-top: 0;
    }
    .libro-info-container {
        padding: 0;
    }
    .libro-info-container {
        width: 45%;
        flex-direction: column;
    }
    .libro-gallery {
        background-color: #F0F0F0;
        padding: 180px 10% 90px 10%;
        margin-top: 0;
    }
    .book-gallery-stage {
        align-items: flex-start;
        flex-direction: row;
        justify-content: space-between;
        flex-wrap: wrap;
    }
    .book-gallery-stage > div {
        flex-direction: column;
        width: 18%;
        height: 100%;
        justify-content:space-evenly;
    }
    .book-gallery-stage > div img {
        margin: 0;
        width: 100%;
    }
    .book-gallery-stage > img {
        width: 78%;
        display: block;
        margin: 0;
    }
    .libro-info {
        padding: 40px 14% 80px 14%;
    }
    .libro-info > div > ul > li {
        font-size: 0.9rem;
    }
    .tags p {
        font-size: 0.8rem;
    }
    .us-header {
        text-align: center;
        padding-top: 150px;
    }
    .us-header > .container {
        max-width: 1000px;
    }
    .us-subtext {
        text-align: center;
        max-width: 1000px;
    }
    .criterio-container {
        padding: 150px 0;
    }
    .criterio-container > .flex {
        max-width: 800px;
    }
    .criterio-titles h3 {
        position: absolute;
        font-size: 9rem;
        top: -4.8rem;
        left: -3.5rem;
        margin: 0;
        font-weight: 800;
    }
    /* BOOKS VIEW */
    .books-header {
        padding: 160px 7% 160px 7%;
        margin-top: 90px;
    }
    .books-header .container h2 {
        margin: 0;
    }
    .header-big-titles {
        font-size: 6rem;
        top: 6.5rem;
    }
    
    .all-books.container {
        margin: 70px auto;
    }
    .all-books {
        display: flex;
        justify-content: space-between;
        padding: 0 5vw;
    }

    .filters {
        padding: 0;
        width: 25%;
    }
    .filters h3 {
        font-size: 1.4rem;
        padding-left: 15px;
        margin-top: 0;
    }
    .filters .MuiAccordionSummary-content > p {
        font-size: 1rem;
    }

    .filters-container .MuiAccordionDetails-root .MuiBox-root.slider > span {
        width: 85%;
    }

    .viewMore {
        display: block;
        opacity: 0;
        background-color: rgba(174, 174, 174, 0.5);
        height: 280px;
        width: 100%;
        position: absolute;
        display: flex;
        align-items: center;
        justify-content: center;
        transition: 0.4s;
    }
    .viewMore img {
        display: block;
        width: 60px;
    }
    .book-card:hover .viewMore {
        opacity: 1;
    }

    .book-cards-container {
        width: 70%;
        display: flex;
    }
    .book-cards-container {
        margin-top: 50px;
    }

    .pagination {
        width: 60%;
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 20px 0 40px 33%;
    }
    .modalBoxImages > img {
        width: 60%;
    }
}


